*{
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: $root-ffamily;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: .7s
}

.element--wrapper {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
    .element--wrapper-content {
        display: block;
    }
}

.body-content--wrapper {
    width: 100%;
    display: block;
    text-align: center
}

.body-content--section {
    background-color: $wrapper-bcolor;
    width: 80%;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: inline-block;
    padding: 0 1em;
}

.body-content--separator {
    border-color: $main-line-color;
    // width: 95%;
}

@media screen and (max-width: 820px) {
    .body-content--section {
        width: 100%;
    }
}