@keyframes rotateIn {
    from {
        transform-origin: center;
        transform: rotate3d(0, 0, 1, 0deg);
    }

    to {
        transform-origin: center;
        transform: rotate3d(0, 0, 1, 360deg);
    }

}

@keyframes flash-animation {
    from,
    50%,
    to {
        opacity: 1;
    }
  
    25%,
    75% {
        opacity: 0;
    }
}