$height-ico: 3em;

.contact--wrapper {
    display: flex;
    color: $paragraph-color;
    justify-content: space-between;
}

.contact-snetworks--wrapper {
    width: 50%;
}

.contact-snetworks--item {
    color: $paragraph-color;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    margin: 1em  2em;

    & .contact-snetworks--item-ico .fab{
        animation-duration: 1s;
        animation-fill-mode: both;
    }

}

.contact-snetworks--item:hover {
    .contact-snetworks--item-ico .fab {
        animation-name: rotateIn;
    }
}

.contact-snetworks--item-ico {
    display: inline-block;
    margin-right: .5em;
    width: $height-ico;
    height: $height-ico;
    line-height: $height-ico;
    border-radius: 50%;
    
    & .fab {
        font-size: 1.2rem;
    }
}

.contact-snetworks--item-label {
    display: inline-block;
    font-weight: bold;
}

.contact-form--wrapper {
    width: 50%;
    padding: 1em;
    background-color: $wrapper-bcolor;
    border: 1px solid $wrapper-bcolor + 10%;
}

.contact-form {
    color: black;

    .contact-form--fieldset {
        width: 100%;
        background-color: #FFFFFF;
        border-width: 0 0 1px 0;
        padding-bottom: 1em;

        & input,
        textarea {
            border: none;
            resize: none;
            width: 100%;
        }
        & input {
            height: 40px;
        }
        &textarea {
            height: 80px;
        }
    }

    .contact-form--button-wrapper {
        width: 100%;
        text-align: right;
        padding: .5em 0;

        & button {
            background-color: white;
            border-radius: .5em;
            padding: .5em;
            width: 25%;
            border: none;
            cursor: pointer;
            font-weight: bold;
        }
        & button:hover {
            & i {
                animation-name: flash-animation;
            }
        }
        
        & button i {
            animation-duration: 1s;
            animation-fill-mode: both;
            padding-left: .5em;
        }
    }
}

@media screen and (max-width: 970px) {
    .contact--wrapper {
        flex-direction: column;

        & .contact-snetworks--wrapper,
        & .contact-form--wrapper {
            width: 100%;
        }
    }
}
