.fixed-navigation--wrapper {
    position: fixed;
    height: 100vh;
    line-height: 100vh;
    right: 0;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

.fixed-navigation--button {
    width: 2.2em;
    height: 2.2em;
    line-height: 2.2em;
    text-align: center;
    border-radius: 50%;
    background-color:#4376ad;
    display: inline-block;
    cursor: pointer;
    margin: .5em;
    color: white;
    font-size: 1.2em;
    transition: .3s;
}

.fixed-navigation--button-active {
    background: #144274;
    background: linear-gradient(90deg, #144274 0%, #2587f3 150%);
    box-shadow: 7px 0px 4px -4px rgba(132, 132, 132, 0.38);
    width: 2.5em;
    height: 2.5em;
    line-height: 2.5em;
    margin: .5em 1.5em .5em .5em;
}