@mixin gen-themes($name, $body-bg-color, $body-bg-texture) {
    body.theme-#{$name} {
        background-color: #{$body-bg-color};
        background-image: url(../images/textures/#{$body-bg-texture});

        @import '../base/_botones.scss';
        @import '../base/_labels.scss';
        
        @import '../components/_info.scss';
        @import '../components/_fixednavigation.scss';
        @import '../components/_proyects.scss';
        @import '../components/_contact.scss';
    }
}

@include gen-themes("black", black, "dust.gif");