$internal-padding: 0 1em;

.proyect-card {
    display: inline-flex;
    flex-direction: column;
    background-color: $wrapper-bcolor;
    border: 1px solid $wrapper-bcolor + 10%;
    margin: .5em 1.5em;
    border-radius: $wrapper-brdradius;
    width: 25%;
    min-height: 320px;
    vertical-align: top;
    justify-content: space-between;
}

.proyect-card:hover {
    box-shadow : 0 3px 5px $wrapper-bcolor + 80%;

    & .proyect-card--link-ico {
        animation-name: flash-animation;
    }
}

.proyect-card--figure-head {
    background-image: url(../images/textures/gplay.png);
    color: $sub-title-label-color;
    text-shadow: 1px 1px 1px $sub-title-label-shadow-color;
    display: flex;
    margin: 0;
    min-height: 4em;
    align-items: center;
    padding: 0 1em;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
}

.proyect-card--description {
    color: $paragraph-color;
    padding: $internal-padding
}

.proyect-card--link {
    float: right;
    padding: .8em;
    margin: .5em;
    text-decoration: none;
    color: $link-label-color;
    border: 1px solid $wrapper-bcolor + 100%;
    border-radius: $wrapper-brdradius;

    .proyect-card--link-text {
        margin-right: .5em;
    }

    .proyect-card--link-ico {
        animation-duration: 1s;
        animation-fill-mode: both;
    }
    
}

@media screen and (max-width: 970px) { 
    .proyect-card {
        width: 45%;
        margin: .5em .2em;
    }
}

@media screen and (max-width: 640px) {
    .proyect-card {
        width: 98%;
        margin: .5em .2em;
    }
}
