.info-card {
    color: white;
}

.info-section {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 70%;
    margin: 0 auto;
}

.info-figure-wrapper {

}

.info-figure-wrapper img {
    width: 150px;
    border-radius: 50%;
}

.info-description {
    color: $paragraph-color;
}

.info-location {
    color: $label-color;
}

.info-tags {
    color: $tag-hash-color;
    width: 60%;
    margin: 0 auto;
    font-weight: bold;
    font-style: italic;
}

.info-tags span {
    margin: 1em;
    display: inline-block;
}

@media screen and (max-width: 820px) {
    .info-section {
        flex-direction: column;
    }
}